import React from "react";

//Icons
import HomeIcon from "@mui/icons-material/Home";
import InfoIcon from "@mui/icons-material/Info";
import MedicalServicesIcon from "@mui/icons-material/MedicalServices";
import ContactPhoneIcon from "@mui/icons-material/ContactPhone";
import MenuIcon from "@mui/icons-material/Menu";

import logo from "../../../images/logo.png";
import classes from "../header/header.module.css";

import { Box } from "@mui/system";
import {
  Button,
  IconButton,
  Menu,
  MenuItem,
  Stack,
  useMediaQuery,
} from "@mui/material";

import PopupState, { bindTrigger, bindMenu } from "material-ui-popup-state";
import { useLocation, useNavigate } from "react-router-dom";

export default function Navbar() {
  const matches = useMediaQuery("(min-width:768px)");
  const navigate = useNavigate();

  const location = useLocation();
  const activePage = location.pathname;

  const navigateToHomePage = () => {
    navigate("/");
  };

  const navigateToAboutUs = () => {
    navigate("/hakkimda");
  };

  const navigateToServices = () => {
    navigate("/hizmetler");
  };

  const navigateToContact = () => {
    navigate("/iletisim");
  };

  const buttonStyle = {
    borderRadius: "2rem",
    padding: "1rem",
    marginRight: "1rem",
    color: "white",
  };
  return (
    <Box
      className={classes.nav}
      sx={{
        backgroundColor: "rgba(255,255,255,.5)",
        zIndex: "2",
        position: "sticky",
        top: 20,
      }}
    >
      <Box display="flex">
        <Box
          marginRight="auto"
          paddingLeft={{ xs: "1rem", sm: "5rem" }}
          zIndex="1"
          onClick={navigateToHomePage}
          style={{ cursor: "pointer" }}
        >
          <img style={{ userSelect: "none" }} src={logo} alt="logo"></img>
        </Box>
        {matches ? (
          <Stack
            direction="row"
            sx={{
              padding: "3rem 5rem 1rem 1rem",
              display: "flex",
              justifyContent: "flex-end",
              height: "3rem",
            }}
            variant="string"
          >
            <Button
              onClick={navigateToHomePage}
              startIcon={<HomeIcon />}
              size="small"
              sx={{
                ...buttonStyle,
                backgroundColor: activePage === "/" ? "#3C4048" : "#28B5B5",
              }}
            >
              Ana Sayfa
            </Button>
            <Button
              size="small"
              onClick={navigateToAboutUs}
              sx={{
                ...buttonStyle,
                backgroundColor:
                  activePage === "/hakkimda" ? "#3C4048" : "#28B5B5",
              }}
              color="inherit"
              endIcon={<InfoIcon />}
            >
              Hakkımda
            </Button>
            <Button
              size="small"
              onClick={navigateToServices}
              sx={{
                ...buttonStyle,
                backgroundColor:
                  activePage === "/hizmetler" ? "#3C4048" : "#28B5B5",
              }}
              color="inherit"
              endIcon={<MedicalServicesIcon />}
            >
              Hizmetler
            </Button>
            <Button
              size="small"
              onClick={navigateToContact}
              sx={{
                ...buttonStyle,
                backgroundColor:
                  activePage === "/iletisim" ? "#3C4048" : "#28B5B5",
              }}
              color="inherit"
              endIcon={<ContactPhoneIcon />}
            >
              İletişim
            </Button>
          </Stack>
        ) : (
          <PopupState variant="popover" popupId="demo-popup-menu">
            {(popupState) => (
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "flex-end",
                }}
                mr={3}
                mb={1}
              >
                <IconButton variant="contained" {...bindTrigger(popupState)}>
                  <MenuIcon />
                </IconButton>
                <Menu {...bindMenu(popupState)}>
                  <MenuItem onClick={popupState.close}>
                    <Button onClick={navigateToHomePage} color="inherit">
                      Ana Sayfa
                    </Button>
                  </MenuItem>
                  <MenuItem onClick={popupState.close}>
                    <Button onClick={navigateToAboutUs} color="inherit">
                      Hakkımda
                    </Button>
                  </MenuItem>
                  <MenuItem onClick={popupState.close}>
                    <Button onClick={navigateToServices} color="inherit">
                      Hizmetler
                    </Button>
                  </MenuItem>
                  <MenuItem onClick={popupState.close}>
                    <Button onClick={navigateToContact} color="inherit">
                      İletişim
                    </Button>
                  </MenuItem>
                </Menu>
              </Box>
            )}
          </PopupState>
        )}
      </Box>
    </Box>
  );
}
