import { Box, Button, Grid, Typography, useMediaQuery } from "@mui/material";
import React, { useEffect, useState } from "react";
import Navbar from "../../components/layout/navBar/navbar";

import classes from "./services.module.css";
import divBg from "../../images/b.jpeg";
import Footer from "../../components/layout/footer/footer";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";

import Card from "../../components/UI/customCard3/card";

import { services, services1, services2 } from "./contents1";
import { data1, data2 } from "./contents1";

import JoyCard from "@mui/joy/Card";
import { AspectRatio } from "@mui/joy";
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function Services(props) {
  const matches = useMediaQuery("(min-width:768px)");

  const [open, setOpen] = useState(false);
  const [open1, setOpen1] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [selectedIndex1, setSelectedIndex1] = useState(0);
  const [selectedIndex2, setSelectedIndex2] = useState(0);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  
  const handleClickOpen = (index) => {
    setSelectedIndex(index);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClickOpen1 = (index) => {
    setSelectedIndex1(index);
    setOpen1(true);
  };

  const handleClose1 = () => {
    setOpen1(false);
  };

   const handleClickOpen2 = (index) => {
     setSelectedIndex2(index);
     setOpen2(true);
   };

   const handleClose2 = () => {
     setOpen2(false);
   };

  return (
    <>
      <div className={classes.header}>
        <div className={classes.image} />
        {matches ? (
          <>
            <Navbar />
            <Box
              mt={3}
              width="98%"
              marginLeft="1%"
              minHeight="30rem"
              height="auto"
              sx={{
                borderRadius: "2rem",
                backgroundColor: "white",
                justifyContent: "center",
                display: "flex",
                backgroundImage: `url(${divBg})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
              }}
            >
              <Grid container spacing={1} m={3} mb={4}>
                {services.map((item, index) => (
                  <Grid item xs={4} sm={6} md={6} lg={4}>
                    <Card
                      action={
                        <Button
                          onClick={() => handleClickOpen(index)}
                          sx={{ marginTop: ".5rem" }}
                          variant="contained"
                          color="info"
                          size="small"
                        >
                          Daha Fazla
                        </Button>
                      }
                      image={item.image}
                      width={360}
                      content={item.content}
                      title={item.title}
                      height={130}
                    />
                  </Grid>
                ))}
              </Grid>
            </Box>
          </>
        ) : (
          <>
            <div className={classes.header}>
              <div className={classes.image}>
                <img src={divBg} alt="div background"></img>
              </div>
              <Navbar />
            </div>
              <Box
              sx={{
                display: "flex",
                gap: 1,
                pt: 3,
                marginBottom: "1rem",
                overflow: "auto",
                width: "95%",
                scrollSnapType: "x mandatory",
                "& > *": {
                  scrollSnapAlign: "center",
                },
                "::-webkit-scrollbar": { display: "none" },
              }}
            >
              {data1.map((item, index) => (
                <JoyCard
                  
                  orientation="horizontal"
                  key={item.title}
                  variant="soft"
                  sx={{
                    gap: 2,
                    "--Card-padding": (theme) => theme.spacing(2),
                    marginTop:3
                  }}
                >
                  <AspectRatio ratio="1" sx={{ minWidth: 100 }}>
                    <img src={item.src} srcSet={item.src} alt={item.title} />
                  </AspectRatio>
                  <Box sx={{ whiteSpace: "nowrap" }}>
                    <Typography fontWeight="lg">{item.title}</Typography>
                    <Typography mt={1.5} level="body2">
                      {item.description}
                    </Typography>
                    <Button
                      onClick={() => handleClickOpen1(index)}
                      color="info"
                      variant="contained"
                      size="small"
                      sx={{
                        marginTop: ".7rem",
                      }}
                    >
                      Daha Fazla
                    </Button>
                  </Box>
                </JoyCard>
              ))}
            </Box>
              <Box
              sx={{
                display: "flex",
                gap: 1,
                pt: 3,
                marginBottom: "1rem",
                overflow: "auto",
                width: "95%",
                scrollSnapType: "x mandatory",
                "& > *": {
                  scrollSnapAlign: "center",
                },
                "::-webkit-scrollbar": { display: "none" },
              }}
            >
              {data2.map((item, index) => (
                <JoyCard
                  orientation="horizontal"
                  key={item.title}
                  variant="soft"
                  sx={{
                    gap: 2,
                    "--Card-padding": (theme) => theme.spacing(2),
                  }}
                  mb={5}
                >
                  <AspectRatio ratio="1" sx={{ minWidth: 100 }}>
                    <img src={item.src} srcSet={item.src} alt={item.title} />
                  </AspectRatio>
                  <Box sx={{ whiteSpace: "nowrap" }}>
                    <Typography fontWeight="lg">{item.title}</Typography>
                    <Typography mt={1.5} level="body2">
                      {item.description}
                    </Typography>
                    <Button
                      onClick={() => handleClickOpen2(index)}
                      color="info"
                      variant="contained"
                      size="small"
                      sx={{
                        marginTop: ".7rem",
                      }}
                    >
                      Daha Fazla
                    </Button>
                  </Box>
                </JoyCard>
              ))}
            </Box>
          </>
        )}
        <Footer />
      </div>
      <Dialog
        TransitionComponent={Transition}
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>{services[selectedIndex].title}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {services[selectedIndex].content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="error" onClick={handleClose}>
            Kapat
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        TransitionComponent={Transition}
        open={open1}
        onClose={handleClose1}
      >
        <DialogTitle>{services1[selectedIndex1].title}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {services1[selectedIndex1].content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="error" onClick={handleClose1}>
            Kapat
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        TransitionComponent={Transition}
        open={open2}
        onClose={handleClose2}
      >
        <DialogTitle>{services2[selectedIndex2].title}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {services2[selectedIndex2].content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="error" onClick={handleClose2}>
            Kapat
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
