import {
  Card,
  CardContent,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";

export default function CustomCard(props) {
  return (
    <Card
      sx={{
        minWidth: props.width,
        minHeight: props.height,
        maxWidth:350,
        opacity: 0.85,
        maxHeight: 680,
        borderRadius: "1rem",
        backgroundColor: "rgba(255,255,255,.8)",
      }}
    >
      <CardContent>
        <Box
          sx={{ display: "flex" }}
          alignItems="center"
          justifyContent="center"
        >
          <img
            style={{ borderRadius: ".5rem" }}
            src={props.image}
            alt="anxiety"
            height="120px"
            width="150px"
          />
          <Typography variant="h6" ml={4}>
            {props.title}
            <Box mt={2}>
              {props.action}
            </Box>
          </Typography>
        </Box>
      </CardContent>
    </Card>
  );
}
